import React, {useState} from 'react'
import logo from '../logo.svg';
import { Counter } from '../features/counter/Counter';
import '../App.css';
import {Button} from '@material-ui/core';
import { geoFindMe } from '../services/gps';
import Header from "../components/Header/Header";

export default function CounterHome() {
    const [open, setOpen] = useState(false);

    const handleOpenNavBar = () => {
        console.log("open ", open);
        setOpen(!open);
    }
    return (
        <header className="App-header">
            <div id="st-container" className={open?"st-container st-effect-1 st-menu-open":"wrapper dashboard-wrapper st-container"}>
                <Header
                    header="Counter Sandbox"
                    openNavBar={handleOpenNavBar}
                    open={open} />
                <div style={{textAlign: 'center', marginTop:30}}>
                    <img src={logo} className="App-logo" alt="logo" />
                    <h1>Brand Inspection</h1>
                    <Counter />
                    <div id="gps-details" style={{margin: '8px auto'}}>
                    </div>
                    <Button variant='outlined' color='primary' onClick={geoFindMe}>my location</Button>
                </div>
            </div>
        </header>
    )
}
